
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import {ConArticleUrl,UploadfileUrl} from '@/api/api'
export default{
data(){
return{
ToastShow:false,
ToastTest:'',
LoadingShow:false,
LoadingWord:' ',
UserInfo:{
name:'',
department:'',
tel:"",
column_name:'',
file_name:'',
file_path:''
}
}
},
components:{
Toast,
Loading
},
methods:{
Uploadfile(){
let str=this.$refs.file.files[0].name
let arr=str.split('.')
let type=arr[arr.length-1]
console.log(type)
if (type!='docx'&&type!='doc'&&type!='txt'&&type!='pdf') {
this.ToastTest='请上传正确的文件格式'
this.ToastShow=true
return
}
let formData = new FormData()
formData.append('file',this.$refs.file.files[0])
this.LoadingShow=true
this.$http.post(UploadfileUrl,formData).then((res)=>{
this.LoadingShow=false
if (res.data.code===0) {
this.ToastTest='上传成功'
this.ToastShow=true
this.UserInfo.column_name=res.data.data.column_name
this.UserInfo.file_name=res.data.data.file_name
this.UserInfo.file_path=res.data.data.file_path
}else{
this.ToastTest=res.data.reason
this.ToastShow=true
}

})
},
SubmitInfo(){
if (this.CheckInfo()) {
this.LoadingShow=true
this.$http.post(ConArticleUrl,this.UserInfo).then((res)=>{
this.LoadingShow=false
if (res.data.code===0) {
this.ToastTest='提交成功'
this.ToastShow=true
}
})
}
},
CheckInfo(){
if (this.UserInfo.name==="") {
this.ToastTest='请输入姓名'
this.ToastShow=true
return false
}
if (!this.checkMobile(this.UserInfo.tel)) {
this.ToastTest='请输入正确的电话号码'
this.ToastShow=true
return false
}
if (this.UserInfo.department==="") {
this.ToastTest='请输入单位'
this.ToastShow=true
return false
}
if (this.UserInfo.column_name===""||this.UserInfo.file_name===""||this.UserInfo.file_path==="") {
this.ToastTest='请上传投稿文件'
this.ToastShow=true
return false
}
return true
},
checkMobile(str){

var
re = /^1\d{10}$/
if (re.test(str)) {
return true
} else {
return false
}
},
}
}



